"use client"

import * as React from "react"
import Link from "next/link"
import {
  Plus,
  Upload,
  Search as SearchIcon,
  MoreHorizontal,
  Calendar,
  User,
  ShieldCheck,
  AlertTriangle,
  FileSearch,
  CheckCircle2,
  XCircle,
  Loader2,
} from "lucide-react"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Separator } from "@/components/ui/separator"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

type Framework = "SOX" | "SOC" | "ISO" | "PCI"
type Risk = "LOW" | "MED" | "HIGH" | "CRITICAL"
type Frequency = "MONTHLY" | "QUARTERLY" | "SEMIANNUAL" | "ANNUAL" | "CONTINUOUS"
type Status = "GREEN" | "YELLOW" | "RED"

type KpiHealth = { green: number; yellow: number; red: number; total: number }
type Control = {
  id: string
  code: string
  name: string
  framework: Framework
  ownerName: string
  focalName: string
  frequency: Frequency
  risk: Risk
  description: string
  lastExecutionAt?: string
  nextExecutionAt?: string
  kpis: KpiHealth
  status: Status
}

type ImportResult = {
  ok: boolean
  summary?: {
    insertedFrameworks: number
    insertedControls: number
    updatedControls: number
    insertedKpis: number
    updatedKpis: number
    rows: number
  }
  errors?: Array<{ row?: number; message: string }>
}

const FRAMEWORK_TABS: Array<{ label: string; value: "ALL" | Framework }> = [
  { label: "SOX", value: "SOX" },
  { label: "SOC", value: "SOC" },
  { label: "ISO", value: "ISO" },
  { label: "PCI", value: "PCI" },
  { label: "Todos", value: "ALL" },
]

const frequencyLabel: Record<Frequency, string> = {
  MONTHLY: "Mensal",
  QUARTERLY: "Trimestral",
  SEMIANNUAL: "Semestral",
  ANNUAL: "Anual",
  CONTINUOUS: "Contínuo",
}

const riskLabel: Record<Risk, string> = {
  LOW: "LOW",
  MED: "MED",
  HIGH: "HIGH",
  CRITICAL: "CRITICAL",
}

function statusLabel(s: Status) {
  if (s === "GREEN") return "Healthy"
  if (s === "YELLOW") return "Attention"
  return "Critical"
}

function statusDotClass(s: Status) {
  if (s === "GREEN") return "bg-emerald-500"
  if (s === "YELLOW") return "bg-amber-500"
  return "bg-rose-600"
}

function statusBadgeVariant(s: Status): "default" | "secondary" | "destructive" {
  if (s === "GREEN") return "secondary"
  if (s === "YELLOW") return "default"
  return "destructive"
}

function riskBadgeClasses(r: Risk) {
  if (r === "LOW") return "bg-muted text-foreground"
  if (r === "MED") return "bg-amber-100 text-amber-900"
  if (r === "HIGH") return "bg-orange-100 text-orange-900"
  return "bg-rose-100 text-rose-900"
}

function frameworkBadgeClasses(f: Framework) {
  if (f === "SOX") return "bg-muted text-foreground"
  if (f === "SOC") return "bg-sky-100 text-sky-900"
  if (f === "ISO") return "bg-indigo-100 text-indigo-900"
  return "bg-violet-100 text-violet-900"
}

function fmtDate(iso?: string) {
  if (!iso) return "—"
  try {
    const d = new Date(iso)
    const dd = String(d.getDate()).padStart(2, "0")
    const mm = String(d.getMonth() + 1).padStart(2, "0")
    const yyyy = d.getFullYear()
    return `${dd}/${mm}/${yyyy}`
  } catch {
    return iso
  }
}

function kpiDots(kpis: KpiHealth) {
  const dots: Status[] = []
  const push = (s: Status, n: number) => {
    for (let i = 0; i < n; i++) dots.push(s)
  }
  push("RED", Math.min(kpis.red, 3))
  push("YELLOW", Math.min(kpis.yellow, 3))
  push("GREEN", Math.min(kpis.green, 3))
  return dots.slice(0, 6)
}

const PAGE_SIZE = 10

export default function ControlsPage() {
  const [mounted, setMounted] = React.useState(false)

  React.useEffect(() => {
    setMounted(true)
  }, [])

  if (!mounted) return null
  const [frameworkTab, setFrameworkTab] = React.useState<"ALL" | Framework>("SOX")
  const [q, setQ] = React.useState("")
  const [risk, setRisk] = React.useState<Risk | "ALL">("ALL")
  const [status, setStatus] = React.useState<Status | "ALL">("ALL")
  const [frequency, setFrequency] = React.useState<Frequency | "ALL">("ALL")
  const [page, setPage] = React.useState(1)

  const [detailOpen, setDetailOpen] = React.useState(false)
  const [importOpen, setImportOpen] = React.useState(false)
  const [newOpen, setNewOpen] = React.useState(false)
  const [selected, setSelected] = React.useState<Control | null>(null)

  // ✅ Dados reais do banco
  const [controls, setControls] = React.useState<Control[]>([])
  const [loading, setLoading] = React.useState(true)
  const [loadError, setLoadError] = React.useState<string | null>(null)

  async function reloadControls() {
    const res = await fetch("/api/controls", { cache: "no-store" })
    if (!res.ok) throw new Error(`Falha ao carregar controles (${res.status})`)
    const data = (await res.json()) as Control[]
    setControls(Array.isArray(data) ? data : [])
  }

  React.useEffect(() => {
    let alive = true
    async function load() {
      try {
        setLoading(true)
        setLoadError(null)
        await reloadControls()
      } catch (e: any) {
        if (!alive) return
        setControls([])
        setLoadError(e?.message ?? "Erro ao carregar controles.")
      } finally {
        if (!alive) return
        setLoading(false)
      }
    }
    load()
    return () => {
      alive = false
    }
  }, [])

  const filtered = React.useMemo(() => {
    const qq = q.trim().toLowerCase()
    return controls.filter((c) => {
      if (frameworkTab !== "ALL" && c.framework !== frameworkTab) return false
      if (risk !== "ALL" && c.risk !== risk) return false
      if (status !== "ALL" && c.status !== status) return false
      if (frequency !== "ALL" && c.frequency !== frequency) return false

      if (qq) {
        const hay = [
          c.code,
          c.name,
          c.ownerName,
          c.focalName,
          c.framework,
          c.risk,
          frequencyLabel[c.frequency],
        ]
          .join(" ")
          .toLowerCase()
        if (!hay.includes(qq)) return false
      }
      return true
    })
  }, [controls, frameworkTab, q, risk, status, frequency])

  React.useEffect(() => {
    setPage(1)
  }, [frameworkTab, q, risk, status, frequency])

  const totals = React.useMemo(() => {
    const base = frameworkTab === "ALL" ? controls : controls.filter((c) => c.framework === frameworkTab)

    const totalControls = base.length
    const attentionControls = base.filter((c) => c.status === "YELLOW").length
    const criticalControls = base.filter((c) => c.status === "RED").length
    const openActionPlans = base.reduce((acc, c) => acc + (c.kpis.red + c.kpis.yellow > 0 ? 1 : 0), 0)

    return { totalControls, attentionControls, criticalControls, openActionPlans }
  }, [controls, frameworkTab])

  const pageCount = Math.max(1, Math.ceil(filtered.length / PAGE_SIZE))
  const pageItems = filtered.slice((page - 1) * PAGE_SIZE, page * PAGE_SIZE)

  function clearFilters() {
    setQ("")
    setRisk("ALL")
    setStatus("ALL")
    setFrequency("ALL")
  }

  function openDetails(c: Control) {
    setSelected(c)
    setDetailOpen(true)
  }

  // =========================
  // IMPORT CSV (UI)
  // =========================
  const fileInputRef = React.useRef<HTMLInputElement | null>(null)
  const [importFile, setImportFile] = React.useState<File | null>(null)
  const [importToken, setImportToken] = React.useState("") // ⚠️ se você usar IMPORT_TOKEN no backend, precisa enviar aqui
  const [importing, setImporting] = React.useState(false)
  const [importResult, setImportResult] = React.useState<ImportResult | null>(null)

  async function runImport() {
    if (!importFile) {
      setImportResult({ ok: false, errors: [{ message: "Selecione um arquivo .csv primeiro." }] })
      return
    }

    try {
      setImporting(true)
      setImportResult(null)

      const fd = new FormData()
      fd.append("file", importFile)

      const res = await fetch("/api/import/controls", {
        method: "POST",
        body: fd,
        headers: importToken ? { Authorization: `Bearer ${importToken}` } : undefined,
      })

      const data = (await res.json()) as ImportResult

      if (!res.ok) {
        setImportResult({
          ok: false,
          errors: data?.errors?.length ? data.errors : [{ message: data?.errors?.[0]?.message ?? `Erro (${res.status})` }],
        })
        return
      }

      setImportResult(data)

      // recarrega lista
      await reloadControls()
    } catch (e: any) {
      setImportResult({ ok: false, errors: [{ message: e?.message ?? "Falha ao importar CSV." }] })
    } finally {
      setImporting(false)
    }
  }

  function resetImportModal() {
    setImportFile(null)
    setImportResult(null)
    setImporting(false)
    // não vou limpar token automaticamente (facilita testar)
    if (fileInputRef.current) fileInputRef.current.value = ""
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col gap-3 sm:flex-row sm:items-start sm:justify-between">
        <div className="space-y-1">
          <h1 className="text-3xl font-bold tracking-tight text-foreground">Controles</h1>
          <p className="text-sm text-muted-foreground">Monitore a saúde dos controles (SOX/SOC/ISO/PCI) e seus KPIs.</p>
        </div>

        <div className="flex flex-col gap-2 sm:flex-row sm:items-center">
          {/* IMPORT */}
          <Dialog
            open={importOpen}
            onOpenChange={(v) => {
              setImportOpen(v)
              if (!v) resetImportModal()
            }}
          >
            <DialogTrigger asChild>
              <Button variant="outline" className="gap-2 bg-transparent">
                <Upload className="h-4 w-4" />
                Importar CSV
              </Button>
            </DialogTrigger>

            <DialogContent className="sm:max-w-[720px]">
              <DialogHeader>
                <DialogTitle>Importar controles + KPIs (CSV)</DialogTitle>
              </DialogHeader>

              <div className="space-y-4 text-sm">
                <div className="rounded-md border p-3 text-muted-foreground">
                  <div className="font-semibold text-foreground">Colunas esperadas no CSV</div>
                  <div className="mt-1 font-mono text-xs">
                    framework,control_code,control_name,control_description,owner_email,focal_email,risk_level,frequency,kpi_code,kpi_name,kpi_description,kpi_criticality,kpi_frequency,kpi_metric,evidence_required,green_min,yellow_min
                  </div>
                  <div className="mt-2">
                    Dica: você pode exportar do Google Sheets como CSV. Aceitamos vírgula ou ponto-e-vírgula.
                  </div>
                </div>

                <div className="grid gap-3 sm:grid-cols-2">
                  <div className="space-y-2">
                    <div className="text-xs font-medium text-foreground">Arquivo</div>
                    <input
                      ref={fileInputRef}
                      type="file"
                      accept=".csv,text/csv"
                      onChange={(e) => setImportFile(e.target.files?.[0] ?? null)}
                      className="block w-full text-sm"
                    />
                    {importFile && (
                      <div className="text-xs text-muted-foreground">
                        Selecionado: <span className="font-semibold text-foreground">{importFile.name}</span>
                      </div>
                    )}
                  </div>

                  <div className="space-y-2">
                    <div className="text-xs font-medium text-foreground">Token (IMPORT_TOKEN)</div>
                    <Input
                      value={importToken}
                      onChange={(e) => setImportToken(e.target.value)}
                      placeholder="Cole aqui o token (Bearer)"
                      type="password"
                    />
                    <div className="text-xs text-muted-foreground">
                      Se o endpoint estiver protegido, você precisa enviar o token aqui.
                    </div>
                  </div>
                </div>

                <Separator />

                <div className="flex items-center justify-between gap-2">
                  <Button variant="outline" className="bg-transparent" onClick={resetImportModal} disabled={importing}>
                    Limpar
                  </Button>

                  <div className="flex items-center gap-2">
                    <Button variant="outline" className="bg-transparent" onClick={() => setImportOpen(false)} disabled={importing}>
                      Fechar
                    </Button>
                    <Button className="gap-2" onClick={runImport} disabled={importing || !importFile}>
                      {importing ? <Loader2 className="h-4 w-4 animate-spin" /> : <Upload className="h-4 w-4" />}
                      {importing ? "Importando..." : "Importar"}
                    </Button>
                  </div>
                </div>

                {/* Resultado */}
                {importResult && (
                  <div className="rounded-md border p-3">
                    {importResult.ok ? (
                      <div className="flex items-start gap-2">
                        <CheckCircle2 className="h-5 w-5 text-emerald-600 mt-0.5" />
                        <div className="space-y-1">
                          <div className="font-semibold">Import concluído</div>
                          {importResult.summary && (
                            <div className="text-xs text-muted-foreground">
                              Linhas: <b>{importResult.summary.rows}</b> • Frameworks +<b>{importResult.summary.insertedFrameworks}</b> •
                              Controles +<b>{importResult.summary.insertedControls}</b> / atualizados <b>{importResult.summary.updatedControls}</b> •
                              KPIs +<b>{importResult.summary.insertedKpis}</b> / atualizados <b>{importResult.summary.updatedKpis}</b>
                            </div>
                          )}
                          <div className="text-xs text-muted-foreground">A lista foi recarregada automaticamente.</div>
                        </div>
                      </div>
                    ) : (
                      <div className="flex items-start gap-2">
                        <XCircle className="h-5 w-5 text-rose-600 mt-0.5" />
                        <div className="space-y-2">
                          <div className="font-semibold">Falha no import</div>
                          <ul className="list-disc pl-5 text-xs text-muted-foreground space-y-1">
                            {(importResult.errors ?? []).slice(0, 10).map((er, i) => (
                              <li key={i}>
                                {typeof er.row === "number" ? <>Linha {er.row}: </> : null}
                                {er.message}
                              </li>
                            ))}
                          </ul>
                          {(importResult.errors?.length ?? 0) > 10 && (
                            <div className="text-xs text-muted-foreground">+ mais erros…</div>
                          )}
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </DialogContent>
          </Dialog>

          {/* NOVO (placeholder) */}
          <Dialog open={newOpen} onOpenChange={setNewOpen}>
            <DialogTrigger asChild>
              <Button className="gap-2">
                <Plus className="h-4 w-4" />
                Novo Controle
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[700px]">
              <DialogHeader>
                <DialogTitle>Novo Controle (placeholder)</DialogTitle>
              </DialogHeader>
              <div className="space-y-4 text-sm text-muted-foreground">
                <p>Cadastro rápido. Depois evolui para formulário completo com KPIs vinculados.</p>
                <Separator />
                <div className="flex justify-end gap-2">
                  <Button variant="outline" className="bg-transparent" onClick={() => setNewOpen(false)}>
                    Cancelar
                  </Button>
                  <Button onClick={() => setNewOpen(false)}>Salvar</Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      {/* Tabs + hint */}
      <div className="flex items-center justify-between gap-4">
        <Tabs value={frameworkTab} onValueChange={(v) => setFrameworkTab(v as any)}>
          <TabsList>
            {FRAMEWORK_TABS.map((t) => (
              <TabsTrigger key={t.value} value={t.value}>
                {t.label}
              </TabsTrigger>
            ))}
          </TabsList>
        </Tabs>

        <div className="hidden sm:flex items-center gap-2 text-xs text-muted-foreground">
          <ShieldCheck className="h-4 w-4" />
          Plataforma interna • extensível para múltiplos frameworks
        </div>
      </div>

      {/* Loading / Error */}
      {loading && (
        <Card>
          <CardContent className="py-8 text-sm text-muted-foreground">Carregando controles do banco...</CardContent>
        </Card>
      )}
      {!loading && loadError && (
        <Card>
          <CardContent className="py-8 text-sm">
            <div className="font-semibold text-destructive">Erro ao carregar</div>
            <div className="text-muted-foreground">{loadError}</div>
          </CardContent>
        </Card>
      )}

      {/* Metrics */}
      <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-xs font-medium text-muted-foreground">TOTAL DE CONTROLES</CardTitle>
          </CardHeader>
          <CardContent className="flex items-end justify-between">
            <div className="text-3xl font-bold">{totals.totalControls}</div>
            <div className="text-sm text-muted-foreground">Ativos</div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-amber-500">
          <CardHeader className="pb-2">
            <CardTitle className="text-xs font-medium text-muted-foreground">CONTROLES EM ATENÇÃO</CardTitle>
          </CardHeader>
          <CardContent className="flex items-end justify-between">
            <div className="text-3xl font-bold text-amber-600">{totals.attentionControls}</div>
            <div className="text-sm text-amber-700">Yellow</div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-rose-600">
          <CardHeader className="pb-2">
            <CardTitle className="text-xs font-medium text-muted-foreground">CONTROLES CRÍTICOS</CardTitle>
          </CardHeader>
          <CardContent className="flex items-end justify-between">
            <div className="text-3xl font-bold text-rose-600">{totals.criticalControls}</div>
            <div className="text-sm text-rose-700">Red</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-xs font-medium text-muted-foreground">PLANOS DE AÇÃO EM ABERTO</CardTitle>
          </CardHeader>
          <CardContent className="flex items-end justify-between">
            <div className="text-3xl font-bold">{totals.openActionPlans}</div>
            <div className="text-sm text-muted-foreground">Non-green</div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="pt-6">
          <div className="grid gap-3 lg:grid-cols-[1.2fr_.7fr_.7fr_.7fr_auto]">
            <div className="relative">
              <SearchIcon className="absolute left-3 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input
                value={q}
                onChange={(e) => setQ(e.target.value)}
                placeholder="Buscar por código, nome, owner ou ponto focal..."
                className="pl-9 h-10"
              />
            </div>

            <Select value={risk} onValueChange={(v) => setRisk(v as any)}>
              <SelectTrigger className="h-10">
                <SelectValue placeholder="Risco" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="ALL">Risco (Todos)</SelectItem>
                <SelectItem value="LOW">Low</SelectItem>
                <SelectItem value="MED">Medium</SelectItem>
                <SelectItem value="HIGH">High</SelectItem>
                <SelectItem value="CRITICAL">Critical</SelectItem>
              </SelectContent>
            </Select>

            <Select value={status} onValueChange={(v) => setStatus(v as any)}>
              <SelectTrigger className="h-10">
                <SelectValue placeholder="Status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="ALL">Status (Todos)</SelectItem>
                <SelectItem value="GREEN">Green</SelectItem>
                <SelectItem value="YELLOW">Yellow</SelectItem>
                <SelectItem value="RED">Red</SelectItem>
              </SelectContent>
            </Select>

            <Select value={frequency} onValueChange={(v) => setFrequency(v as any)}>
              <SelectTrigger className="h-10">
                <SelectValue placeholder="Frequência" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="ALL">Frequência (Todas)</SelectItem>
                <SelectItem value="MONTHLY">Mensal</SelectItem>
                <SelectItem value="QUARTERLY">Trimestral</SelectItem>
                <SelectItem value="SEMIANNUAL">Semestral</SelectItem>
                <SelectItem value="ANNUAL">Anual</SelectItem>
                <SelectItem value="CONTINUOUS">Contínuo</SelectItem>
              </SelectContent>
            </Select>

            <div className="flex items-center justify-end">
              <Button variant="ghost" className="h-10 text-primary" onClick={clearFilters}>
                Limpar filtros
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Table */}
      <Card>
        <CardContent className="pt-6">
          <div className="rounded-lg border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-[260px]">CONTROLE</TableHead>
                  <TableHead>FRAMEWORK</TableHead>
                  <TableHead>OWNER / PONTO FOCAL</TableHead>
                  <TableHead>FREQUÊNCIA</TableHead>
                  <TableHead>RISCO</TableHead>
                  <TableHead>STATUS</TableHead>
                  <TableHead>ÚLT. EXEC.</TableHead>
                  <TableHead>PRÓX. EXEC.</TableHead>
                  <TableHead>KPIs</TableHead>
                  <TableHead className="text-right">AÇÕES</TableHead>
                </TableRow>
              </TableHeader>

              <TableBody>
                {pageItems.length === 0 ? (
                  <TableRow>
                    <TableCell colSpan={10} className="py-12">
                      <div className="mx-auto flex max-w-[520px] flex-col items-center gap-2 text-center">
                        <FileSearch className="h-8 w-8 text-muted-foreground" />
                        <div className="text-base font-semibold">Nenhum controle encontrado</div>
                        <div className="text-sm text-muted-foreground">
                          Ajuste sua busca ou limpe os filtros para ver mais resultados.
                        </div>
                        <Button variant="outline" className="bg-transparent" onClick={clearFilters}>
                          Limpar filtros
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ) : (
                  pageItems.map((c) => (
                    <TableRow key={c.id} className="cursor-pointer hover:bg-muted/50" onClick={() => openDetails(c)}>
                      <TableCell>
                        <div className="text-xs text-muted-foreground">{c.code}</div>
                        <div className="font-semibold">{c.name}</div>
                      </TableCell>

                      <TableCell>
                        <Badge className={frameworkBadgeClasses(c.framework)}>{c.framework}</Badge>
                      </TableCell>

                      <TableCell>
                        <div className="flex flex-col">
                          <span className="font-semibold">{c.ownerName}</span>
                          <span className="text-xs text-muted-foreground">{c.focalName}</span>
                        </div>
                      </TableCell>

                      <TableCell>
                        <Badge variant="outline">{frequencyLabel[c.frequency]}</Badge>
                      </TableCell>

                      <TableCell>
                        <Badge className={riskBadgeClasses(c.risk)}>{riskLabel[c.risk]}</Badge>
                      </TableCell>

                      <TableCell>
                        <div className="flex items-center gap-2">
                          <span className={`h-2.5 w-2.5 rounded-full ${statusDotClass(c.status)}`} />
                          <Badge variant={statusBadgeVariant(c.status)}>{statusLabel(c.status)}</Badge>
                        </div>
                      </TableCell>

                      <TableCell className="text-muted-foreground">{fmtDate(c.lastExecutionAt)}</TableCell>
                      <TableCell className="text-muted-foreground">{fmtDate(c.nextExecutionAt)}</TableCell>

                      <TableCell>
                        <div className="flex items-center gap-3">
                          <Link
                            href={`/controls/${encodeURIComponent(c.id)}`}
                            className="text-sm font-semibold text-primary hover:underline"
                            onClick={(e) => e.stopPropagation()}
                          >
                            {c.kpis.total} KPIs
                          </Link>
                          <div className="flex items-center gap-1">
                            {kpiDots(c.kpis).map((d, idx) => (
                              <span
                                key={`${c.id}-dot-${idx}`}
                                className={`h-2 w-2 rounded-full ${
                                  d === "GREEN" ? "bg-emerald-500" : d === "YELLOW" ? "bg-amber-500" : "bg-rose-600"
                                }`}
                                title={d}
                              />
                            ))}
                          </div>
                        </div>
                      </TableCell>

                      <TableCell className="text-right" onClick={(e) => e.stopPropagation()}>
                        <DropdownMenu>
                          <DropdownMenuTrigger asChild>
                            <Button variant="ghost" size="icon" className="h-8 w-8">
                              <MoreHorizontal className="h-4 w-4" />
                            </Button>
                          </DropdownMenuTrigger>
                          <DropdownMenuContent align="end" className="w-56">
                            <DropdownMenuLabel>Ações</DropdownMenuLabel>
                            <DropdownMenuSeparator />
                            <DropdownMenuItem onClick={() => openDetails(c)}>Ver detalhes</DropdownMenuItem>
                            <DropdownMenuItem asChild>
                              <Link href={`/execution?control=${encodeURIComponent(c.id)}`}>Registrar execução</Link>
                            </DropdownMenuItem>
                            <DropdownMenuItem asChild>
                              <Link href={`/planos-acao?control=${encodeURIComponent(c.id)}`}>Ver planos de ação</Link>
                            </DropdownMenuItem>
                            <DropdownMenuSeparator />
                            <DropdownMenuItem disabled>Editar controle (em breve)</DropdownMenuItem>
                          </DropdownMenuContent>
                        </DropdownMenu>
                      </TableCell>
                    </TableRow>
                  ))
                )}
              </TableBody>
            </Table>
          </div>

          {/* Pagination */}
          {filtered.length > 0 && (
            <div className="mt-4 flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between">
              <div className="text-sm text-muted-foreground">
                Mostrando <span className="font-semibold text-foreground">{(page - 1) * PAGE_SIZE + 1}</span> a{" "}
                <span className="font-semibold text-foreground">{Math.min(page * PAGE_SIZE, filtered.length)}</span> de{" "}
                <span className="font-semibold text-foreground">{filtered.length}</span> controles
              </div>

              <div className="flex items-center gap-2">
                <Button
                  variant="outline"
                  className="bg-transparent"
                  size="sm"
                  onClick={() => setPage((p) => Math.max(1, p - 1))}
                  disabled={page <= 1}
                >
                  Anterior
                </Button>

                <div className="text-sm text-muted-foreground">
                  Página <span className="font-semibold text-foreground">{page}</span> /{" "}
                  <span className="font-semibold text-foreground">{pageCount}</span>
                </div>

                <Button
                  variant="outline"
                  className="bg-transparent"
                  size="sm"
                  onClick={() => setPage((p) => Math.min(pageCount, p + 1))}
                  disabled={page >= pageCount}
                >
                  Próxima
                </Button>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Details Modal (igual ao seu) */}
      <Dialog open={detailOpen} onOpenChange={setDetailOpen}>
        <DialogContent className="sm:max-w-[900px]">
          <DialogHeader>
            <DialogTitle>Detalhes do Controle</DialogTitle>
          </DialogHeader>

          {!selected ? (
            <div className="text-sm text-muted-foreground">Selecione um controle para ver detalhes.</div>
          ) : (
            <div className="space-y-5">
              <div className="flex flex-col gap-3 sm:flex-row sm:items-start sm:justify-between">
                <div className="space-y-1">
                  <div className="text-xs text-muted-foreground">{selected.code}</div>
                  <div className="text-xl font-semibold">{selected.name}</div>
                  <div className="flex flex-wrap items-center gap-2 pt-1">
                    <Badge className={frameworkBadgeClasses(selected.framework)}>{selected.framework}</Badge>
                    <Badge className={riskBadgeClasses(selected.risk)}>{riskLabel[selected.risk]}</Badge>
                    <div className="flex items-center gap-2">
                      <span className={`h-2.5 w-2.5 rounded-full ${statusDotClass(selected.status)}`} />
                      <Badge variant={statusBadgeVariant(selected.status)}>{statusLabel(selected.status)}</Badge>
                    </div>
                    <Badge variant="outline">{frequencyLabel[selected.frequency]}</Badge>
                  </div>
                </div>

                <div className="flex flex-col gap-2 sm:items-end">
                  <div className="flex gap-2">
                    <Button asChild variant="outline" className="gap-2 bg-transparent">
                      <Link href={`/planos-acao?control=${encodeURIComponent(selected.id)}`}>
                        <AlertTriangle className="h-4 w-4" />
                        Planos de ação
                      </Link>
                    </Button>
                    <Button asChild className="gap-2">
                      <Link href={`/execution?control=${encodeURIComponent(selected.id)}`}>
                        <Calendar className="h-4 w-4" />
                        Registrar execução
                      </Link>
                    </Button>
                  </div>

                  <div className="text-xs text-muted-foreground">
                    <span className="inline-flex items-center gap-1">
                      <User className="h-3.5 w-3.5" /> Owner:{" "}
                      <span className="text-foreground">{selected.ownerName}</span>
                    </span>{" "}
                    •{" "}
                    <span className="inline-flex items-center gap-1">
                      <User className="h-3.5 w-3.5" /> Ponto focal:{" "}
                      <span className="text-foreground">{selected.focalName}</span>
                    </span>
                  </div>
                </div>
              </div>

              <Separator />

              <div className="grid gap-4 lg:grid-cols-3">
                <Card className="lg:col-span-2">
                  <CardHeader className="pb-2">
                    <CardTitle className="text-sm">Descrição / Desenho do controle</CardTitle>
                  </CardHeader>
                  <CardContent className="text-sm text-muted-foreground">{selected.description}</CardContent>
                </Card>

                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-sm">Execução</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-2 text-sm">
                    <div className="flex items-center justify-between">
                      <span className="text-muted-foreground">Última execução</span>
                      <span className="font-semibold">{fmtDate(selected.lastExecutionAt)}</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-muted-foreground">Próxima execução</span>
                      <span className="font-semibold">{fmtDate(selected.nextExecutionAt)}</span>
                    </div>

                    <Separator />

                    <div className="space-y-1">
                      <div className="text-xs font-semibold text-foreground">Saúde dos KPIs</div>
                      <div className="flex flex-wrap gap-2">
                        <Badge variant="outline">Total: {selected.kpis.total}</Badge>
                        <Badge className="bg-emerald-100 text-emerald-900">Green: {selected.kpis.green}</Badge>
                        <Badge className="bg-amber-100 text-amber-900">Yellow: {selected.kpis.yellow}</Badge>
                        <Badge className="bg-rose-100 text-rose-900">Red: {selected.kpis.red}</Badge>
                      </div>
                      <div className="pt-2">
                        <Button asChild variant="outline" className="bg-transparent" size="sm">
                          <Link href={`/controls/${encodeURIComponent(selected.id)}`}>Ver KPIs e histórico (em breve)</Link>
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </div>
          )}
        </DialogContent>
      </Dialog>
    </div>
  )
}
